@font-face {
  font-family: "SourceHanSerif";
  font-weight: 900;
  src: url("./SourceHanSerif-Heavy-tate.otf") format('opentype');
}

*,*::before,*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}


.wrapper {
  display: flex;
  flex-direction: column;
}

@media screen {
  html {
    background: gray;
  }
  body {
    padding: 10px;
  }

  svg {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    height: auto;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,.5);
  }

  svg + svg {
    margin-top: 10px;
  }
}

@media print {
  svg {
    width: 297mm;
    height: 210mm;
    page-break-after: always;
  }

  @page {
    margin: 0;
    size: A4 landscape;
  }
}

.noshi__omotegaki {
  writing-mode: vertical-rl;
  text-anchor: middle;
  font-family: SourceHanSerif;
  font-weight: 900;
  font-size: 10px;
  transform: translate3d(4px,0,0);
}

.noshi__omotegaki .noshi__text {
  letter-spacing: -4px;
}

.noshi__name {
  writing-mode: vertical-rl;
  font-family: SourceHanSerif;
  font-weight: 900;
  font-size: 5px;
  transform: translate3d(4px,0,0);
}

.noshi__name .noshi__text {
  letter-spacing: -2px;
}

.noshi__text--hankaku {
  letter-spacing: 0px !important;
}
